// layouts / Header


// Modules
import React from 'react';
import Link from "gatsby-link";

// Layouts
import Navigation from "./Navigation";

// Styles
import styles from '../style/landing.module.css';


export default class Header extends React.Component {
    componentDidMount() {
        // add root container for styling
        $("#___gatsby").addClass(styles.gatsbyContainer);
    }

    render() {
        let pictures = (
            <span>
                <figure className={styles.sun1}></figure>
                <figure className={styles.cloud1a}></figure>
                <figure className={styles.cloud1b}></figure>
                <figure className={styles.cloud2a}></figure>
                <figure className={styles.cloud2b}></figure>
                <figure className={styles.mountain1}></figure>
                <figure className={styles.mountain2}></figure>
                <figure className={styles.house1}></figure>
                <figure className={styles.house2}></figure>
                <figure className={styles.tree1a}></figure>
                <figure className={styles.tree1b}></figure>
                <figure className={styles.tree2a}></figure>
                <figure className={styles.tree2b}></figure>
                <figure className={styles.bushes1a}></figure>
                <figure className={styles.bushes1b}></figure>
            </span>
        );

        return (
            <header className={styles.section1}>
                <Navigation atLandingPage={true}/>

                <span>
                    <h1 className={styles.section1Title1}>Communication tools for residents and managers</h1>
                    <Link className={styles.section1Btn1} to="/login">Get Started Today</Link>
                </span>

            </header>
        );
    }
};



