// Components / LoginForm

// Modules
import React from "react";

// Styles
import styles from '../../style/landing.module.css';


export default class ManagerBlock extends React.Component {
    render() {
        return (
            <div className={styles.residentBlock}>
                <div className={styles.residentB1}>
                    <h3 className={styles.residentB1Num}>01.</h3>
                    <h3 className={styles.residentB1Title}>Registration as Property Manager</h3>
                    <div className={styles.residentB1Inner}>
                        <div className={styles.residentB1InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB1InnerRowFigure1}></figure>
                            <p className={styles.residentB1InnerRowText}>
                                Fill in the details of your property
                            </p>
                        </div>

                        <div className={styles.residentB1InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB1InnerRowFigure2}></figure>
                            <p className={styles.residentB1InnerRowText}>
                                Choose the subscription plan that suit your management requirement
                            </p>
                        </div>

                        <div className={styles.residentB1InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB1InnerRowFigure3}></figure>
                            <p className={styles.residentB1InnerRowText}>
                                Flexible payment options
                            </p>
                        </div>
                    </div>
                </div>

                <div className={styles.residentB2}>
                    <h3 className={styles.residentB2Num}>02.</h3>
                    <h3 className={styles.residentB2Title}>Distribute residents account</h3>
                    <div className={styles.residentB2Inner}>
                        <div className={styles.residentB2InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB2InnerRowFigure1}></figure>
                            <p className={styles.residentB2InnerRowText}>
                                Residents able to log into the channel that you control
                            </p>
                        </div>

                        <div className={styles.residentB2InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB2InnerRowFigure2}></figure>
                            <p className={styles.residentB2InnerRowText}>
                                Provide seamless experience for residents to cooperate with management team
                            </p>
                        </div>

                        <div className={styles.residentB2InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB2InnerRowFigure3}></figure>
                            <p className={styles.residentB2InnerRowText}>
                                Real-time pushing announcements / notification / information to your residents
                            </p>
                        </div>
                    </div>
                </div>

                <div className={styles.residentB3}>
                    <h3 className={styles.residentB3Num}>03.</h3>
                    <h3 className={styles.residentB3Title}>Monitoring at ease</h3>
                    <div className={styles.residentB3Inner}>
                        <div className={styles.residentB3InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB3InnerRowFigure1}></figure>
                            <p className={styles.residentB3InnerRowText}>
                                Let Residency.my platform take care of your management
                            </p>
                        </div>

                        <div className={styles.residentB3InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB3InnerRowFigure2}></figure>
                            <p className={styles.residentB3InnerRowText}>
                                Receive deep insights about the information of your property
                            </p>
                        </div>

                        <div className={styles.residentB1InnerRow}>
                            <figure className={styles.residentBlockFigure + " " + styles.residentB3InnerRowFigure3}></figure>
                            <p className={styles.residentB1InnerRowText}>
                                Perceive real-time feedback / complaints / information from your residents
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
};



