// pages / index

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link from "gatsby-link";

// Layouts
import Header from '../layouts/Header';

// Components
import ResidentBlock from '../components/landing-page/ResidentBlock';
import ManagerBlock from '../components/landing-page/ManagerBlock';

// Actions
import {changeBlockType} from '../action/LandingPageAction';

// Styles
import styles from '../style/landing.module.css';

class HomePage extends React.Component {
    changeBlock(arg) {
        this.props.changeBlockType(arg);
    }

    componentDidMount() {
        /*-------- Scroll To Top ---------- */
        $('#___gatsby').scroll(function() {
            if ($(this).scrollTop() > 300) {
                $('#back-top').fadeIn();
            } else {
                $('#back-top').fadeOut();
            }
        });
        // scroll body to 0px on click
        $('#back-top').on('click', function() {
            $('#___gatsby').animate({
                scrollTop: 0
            }, 1500, 'easeInOutExpo');
            return false;
        });

        $('a.page-scroll').bind('click', function(event) {
            var $anchor = $(this);
            $('#___gatsby').stop().animate({
                scrollTop: $($anchor.attr('href')).offset().top
            }, 1500, 'easeInOutExpo');
            event.preventDefault();
        });
    }

    render() {
        return (
            <div className={styles.wrapperInner}>

                <Helmet title="Residency.my - offers more than just communication tools to residents and managers" meta={[
                    {
                        name: 'description',
                        content: 'Offers more than just communication tools to residents and managers'
                    }, {
                        name: 'keywords',
                        content: 'Residency.my'
                    }
                ]}/>

              <Header/>

                <section className={styles.section2} id="features">
                    <h1 className={styles.section2Title1}>
                        Our website features a wide range of services catering to condominium, apartments, and closed residences. Lets take a look at them.
                    </h1>

                    <div className={styles.featuresHolder}>
                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureBooking}></figure>
                            <h3 className={styles.featuresTitle}>Bookings</h3>
                            <p className={styles.featuresText}>
                                You can book facilities in lesser time and get approvals instantly Example: BBQ pits, Condo Hall, and etc.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureManagement}></figure>
                            <h3 className={styles.featuresTitle}>Visitor Management</h3>
                            <p className={styles.featuresText}>
                                In this feature, you can get your visitors registered and approved before they make an appearance. You can also make pre-car parking bookings to ensure a hassle free visit.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureComplain}></figure>
                            <h3 className={styles.featuresTitle}>Complain</h3>
                            <p className={styles.featuresText}>
                                You have complains and no one to cater? With this feature, you can file your complains to our managers 24/7. Each complains is directly dealt by the property managers to avoid wasting time and ensure prompt and smooth resolution.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureMessage}></figure>
                            <h3 className={styles.featuresTitle}>Message</h3>
                            <p className={styles.featuresText}>
                                This feature allows you to connect with the property managers directly. Whether it is an inquiry, a public announcement, maintenance notice or just a Hi! All your messages will be directly sent to your property managers devices.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureForum}></figure>
                            <h3 className={styles.featuresTitle}>Forum</h3>
                            <p className={styles.featuresText}>
                                This is interesting! You can interact with your fellow neighbours and get responses. Similar to a social platform, the management can post important updates, make announcements, maintenance notices and etc.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figurePayments}></figure>
                            <h3 className={styles.featuresTitle}>Payments</h3>
                            <p className={styles.featuresText}>
                                Coming soon, with this feature, you can pay your utilities, management and all payments related to your home via credit card, debit card and internet banking.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figurePaymentHistory}></figure>
                            <h3 className={styles.featuresTitle}>Payment History</h3>
                            <p className={styles.featuresText}>
                                Home Management is time taking and crucial. But did you know it would become so easy with our Payment History feature? This part allows you to view all the transactions and payments that you have made in the past for your utility and management fees.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureSecurity}></figure>
                            <h3 className={styles.featuresTitle}>Security</h3>
                            <p className={styles.featuresText}>
                                Suddenly you drive in your condo and notice a new face in the guard house? This is no more happening because we take security seriously. Our security feature will get notify you guard profile of the day so that you recognise and know your guard better. The messenger feature will also allow you to speak to your security officer at any time.
                            </p>
                        </div>

                        <div className={styles.features}>
                            <figure className={styles.section2Figure + ' ' + styles.figureClassified}></figure>
                            <h3 className={styles.featuresTitle}>Classified</h3>
                            <p className={styles.featuresText}>
                                Need a fixer to resolve your household issue? This area will you out in finding out trusted handyman to fix your homes, a babysitter, housekeeper and other services at ease.
                            </p>
                        </div>
                    </div>

                </section>

                <section className={styles.section4} id="how-it-works">
                    <h1 className={styles.section4Title1}>
                        How it works?
                    </h1>
                    <div className={styles.section4Buttons}>
                        <button type="button" onClick={() => this.changeBlock("residentBlock")} className={this.props.landingPage.blockType === 'residentBlock'
                            ? (styles.section4Button1 + ' ' + styles.active)
                            : (styles.section4Button1)}>
                            For Resident
                        </button>
                        <button type="button" onClick={() => this.changeBlock("managerBlock")} className={this.props.landingPage.blockType === 'managerBlock'
                            ? (styles.section4Button1 + ' ' + styles.active)
                            : (styles.section4Button1)}>
                            For Property Manager
                        </button>
                    </div>

                    {this.props.landingPage.blockType === "residentBlock"
                        ? <ResidentBlock/>
                        : <ManagerBlock/>
}
                </section>

                <section className={styles.section5}>
                    <div className={styles.section5Block}>
                        <h3 className={styles.section5Title}>
                            Residency.my provides residents with easy solutions keeping you away from complications and hurdles. With no installations, the tools can be accessed through our website and you are good to go!
                        </h3>
                        <Link className={styles.section5Btn1} to="/login">Get Started Today</Link>
                    </div>

                    <figure className={styles.house3}></figure>
                    <figure className={styles.tree2c}></figure>
                    <figure className={styles.bushes1c}></figure>
                </section>

                <footer className={styles.footer}>
                    <div className={styles.footerBlock}>
                        <div className={styles.footerBlockcolumn}>
                            <h3 className={styles.footerBlockTitle}>About Residency.my</h3>
                            <Link className={styles.footerBlockLink} to="#">About Us</Link>
                            <Link className={styles.footerBlockLink} to="#">Our Mission</Link>
                            <Link className={styles.footerBlockLink} to="#">Blog</Link>
                        </div>

                        <div className={styles.footerBlockcolumn}>
                            <h3 className={styles.footerBlockTitle}>Resources</h3>
                            <Link className={styles.footerBlockLink} to="#">Support</Link>
                            <Link className={styles.footerBlockLink} to="#">Privacy &amp; Terms</Link>
                        </div>

                        <div className={styles.footerBlockcolumn}>
                            <h3 className={styles.footerBlockTitle}>Get in touch</h3>
                            <p className={styles.footerBlockLink}>+012 34567890</p>
                            <a className={styles.footerBlockLink} href="mailto:support@residency.my">support@residency.my</a>
                        </div>
                    </div>

                    <p className={styles.footerCopyright}>
                        Copyright &copy; 2017 Residency.my All Rights Reserved.
                    </p>
                </footer>

                <a id="back-top" className={styles.backToTop} href="#">
                    <i className={styles.backToTopIcon + " fa fa-angle-up"}></i>
                </a>

            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {landingPage: state.landingPage};
}

const mapDispatchToProps = (dispatch) => {
    return {changeBlockType};
}

export default connect(mapStateToProps, mapDispatchToProps())(HomePage);



